//async paginate burada kullanlacak
import { AsyncPaginate } from "react-select-async-paginate";
import { useState } from "react";
import { GEO_API_URL, geoApiOptions } from "../api";

const Search = ({ onSearchChange }) => { //onSearchChange func. u parametre olarak geiyoruz. app.js de kullanlyor.
  const [search, setSearch] = useState(null);

  const loadOptions = (inputValue) => {
    return fetch(
      `${GEO_API_URL}/cities?minPopulation=1000000&namePrefix=${inputValue}`,
      geoApiOptions
    )
      .then((response) => response.json())
      .then((response) => {
        return {
          //obje array i require ediyor api. input a girilen datay editliyoruz.
/*latitude ve longitude openweather API si tarafndan istenen bir ey. O yzden rapidApi sitesinden(GeoAP) ehirlerin lat ve lon unu buradan alyoruz.
Bylece lot ve lon a gre ehri tanyp hava durumunu verecek bize openweather api si. latitude ve longitude u Openweather api si istedii iin kullanmak zorundayz */          
          options: response.data.map((e) => {
            return {
              value: `${e.latitude} ${e.longitude}`, 
              label: `${e.name} ${e.countryCode}`,
            };
          }),
        };
      })
      .catch((err) => console.error(err));
  };

  const handleOnChange = (searchData) => {
    //paginate e girilen data y parametre alr.
    setSearch(searchData);
    onSearchChange(searchData);
  };

  return (
    <AsyncPaginate
      placeholder="Search for city"
      debounceTimeout={600} //API ye gnderilen request leri snrlama. 600 ms.
      value={search}
      onChange={handleOnChange}
      loadOptions={loadOptions}
    />
  );
};

export default Search;
